.note{
    background: #fff;
    border-radius: 7px;
    padding: 10px;
    width: 90vw;
    margin: 16px;
    float: left;

    box-shadow:
    0 1.8px 2.2px rgba(0, 0, 0, 0.014),
    0 4.7px 5.3px rgba(0, 0, 0, 0.028),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 12.3px 17.9px rgba(0, 0, 0, 0.042),
    0 21.8px 33.4px rgba(0, 0, 0, 0.046),
    0 50px 80px rgba(0, 0, 0, 0.09)

}

.note h1{

    font-size: 1.5rem;
    margin-bottom: 6px;
    font-family: Arial, Helvetica, sans-serif;
    
}

.note p{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.note button{
    background-color: #8CC0DE;
}
.note:hover{
    transform: scale(1.1);
}